.header {
  display: flex;
  max-width: 1920px;
  height: 88px;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  background: #1c1c21;
  box-shadow: 0px 0px 1px 0px rgba(0, 0, 0, 0.04),
    0px 4px 8px 0px rgba(0, 0, 0, 0.04), 0px 16px 24px 0px rgba(0, 0, 0, 0.04),
    0px 24px 32px 0px rgba(0, 0, 0, 0.04);
}

.nav {
  width: 100%;
  max-width: 1280px;
  height: 56px;
  display: flex;
  align-items: center;
  margin: 0 auto;
}

.icon {
  width: 24px;
  height: 24px;
}

.logo {
  margin-left: 112px;
  margin-right: auto;
}

.link {
  display: flex;
  align-items: center;
  gap: 8px;
  border-radius: 40px;
  border: none;
  background: #1c1c21;
  height: 56px;
  color: #8585ad;
  text-decoration: none;
  justify-content: center;
}

.link_active {
  color: #f2f2f3;
}

.inActive {
  color: #f2f2f3;
}
